___CSS_LOADER_EXPORT___.push([module.id, `body {
    margin: 0;
    padding: 0;
    height: 100vh;
    background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) no-repeat;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: cursive;
}

#content {
    height: 75vh;
    width: 800px;
    background-color: #e03e00;
    border: 1px solid transparent;
    border-radius: 25px;
}

header {
    height: 15vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.header {
    margin: 35px auto;
    width: 350px;
    background: white;
    padding: 15px;
    text-align: center;
    border-radius: 25px;
}

.content {
    min-height: 200px;
    margin: 35px auto;
    width: 450px;
    background: white;
    padding: 15px;
    border-radius: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.header h3 {
    font-size: x-large;
    font-family: cursive;
}

.nav-btn {
    background-color: #ff6347; /* Tomato color */
    color: white;
    border: none;
    padding: 15px 30px;
    font-size: 16px;
    cursor: pointer;
    border-radius: 5px;
    margin: 0 15px;
    transition: background-color 0.3s, transform 0.3s;
}

.nav-btn:hover {
    background-color: #ff4500; /* OrangeRed color */
    transform: scale(1.05);
}

.nav-btn:active {
    background-color: #e03e00; /* Darker OrangeRed */
}

.nav-btn:focus {
    outline: none;
    box-shadow: 0 0 0 3px rgba(255, 99, 71, 0.5);
}
